import { React } from 'jimu-core';

export const Link = (props) => {
    const { url } = props.contentState.getEntity(props.entityKey).getData();
    return (
        <a href={url} style={{
            color: '#3b5998',
            textDecoration: 'underline',
        }}>
            {props.children}
        </a>
    );
};